import React, { Component } from "react";
import {getDasboardData} from "../dashboardService"


class Dashboard1 extends Component {
  state = {
    cardList1: [],
    cardList2:[],
    bardana:[],
  };
  componentDidMount() {
    getDasboardData(this.props.partyType,this.props.search).then(res => this.setState({ cardList1: res.data.pending, cardList2: res.data.total, bardana: res.data.bardana }));
  }
  getUserStatusClass = (status) => {
    switch (status) {
      case "active":
        return "badge-success";
      case "inactive":
        return "badge-warning";
      case "pending":
        return "badge-primary";
      default:
        break;
    }
  };
  render() {
    let {
      cardList1 = [],
      cardList2 = [],
      bardana = [],
    } = this.state;
    return (
      <div>
        <div className="row">
          { Object.keys(cardList1).map((card, index) => (
            <div key={index} className="col-lg-4 col-md-6 col-sm-6">
              <div className="card card-icon-bg bg-info o-hidden mb-4">
                <div>
                  <div className="mt-2 mb-2 text-24 text-white text-center">
                   {card}
                  </div>
                  <div className="content">
                    <table className="table table-borderless mx-2">
                      {cardList1[card].map((item, index)=>(
                          <tr key={index} className="text-white mt-2 mb-0 text-capitalize">
                            <th className="text-18"> {item.genes} </th>
                            <td className="lead text-24 text-white">{item.pending__sum} {item.unit} </td>
                          </tr>
                      ))}
                      </table>
                  </div>
                </div>
              </div>
            </div>
          ))}
        
         
            <div className="col-lg-4 col-md-6 col-sm-6">
              <div className="card card-icon-bg o-hidden mb-4" style={{backgroundColor:"#b9a37a"}}>
                <div>
                  <div className="mt-2 mb-2 text-24 text-white text-center">
                   {"Bardana"}
                  </div>
                  <div className="content">
                    <table className="table table-borderless mx-2">
                      {bardana?.map((item, index)=>(
                          <tr key={index} className="text-white mt-2 mb-0 text-capitalize">
                            <td className="text-18"> {item.party_name} </td>
                            <td className="lead text-24 text-white">{item.quality} </td>
                            <td className="lead text-24 text-white">{item.quantity} BAGS </td>
                          </tr>
                      ))}
                      </table>
                  </div>
                </div>
              </div>
            </div>
        
        
          { Object.keys(cardList2).map((card, index) => (
            <div key={index} className="col-lg-4 col-md-6 col-sm-6">
              <div className="card card-icon-bg bg-success o-hidden mb-4">
                <div>
                  <div className="text-white mt-2 mb-2 text-24 text-center">
                   {card}
                  </div>
                  <div className="content">
                    <table className="table table-borderless mx-2">
                      {cardList2[card].map((item, index)=>(
                          <tr key={index} className="text-white mt-2 mb-0 text-capitalize">
                            <th className="text-18"> {item.genes} </th>
                            <td className="lead text-24 text-white">{item.quantity__sum} {item.unit} </td>
                          </tr>
                      ))}
                      </table>
                  </div>
                </div>
              </div>
            </div>
          ))}
          </div>
      </div>
    );
  }
}

export default Dashboard1;
